.calculator {
    width: 300px;
    height: 500px;
    background-color: darkviolet;
    margin: auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-top: 90px;

  }
  
  .display {
    font-size: 3rem;
    margin-bottom: 40px;
    margin-top: 80px;
    padding: 10px;
    border: none;
    background-color: rgb(17, 253, 253);
    border-radius: 10px;
    text-align: right;
    height: 60px;
    
  }
  
  .buttons {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10px;
    color: aqua;
    border:none;
    margin-top: 10px;
  }
  
  button {
    font-size: 18px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 3px;
    cursor: pointer;
    color: rgb(255, 255, 255);
    background-color: rgb(11, 11, 232);
  }
  button:hover{
    background-color: rgb(251, 2, 2);
  }
  .head{
    font-family: cursive;
    font-style: oblique;
    font-weight: 400;
    font-size: 1.5rem;
    margin-left: 20px;
    color: rgb(255, 255, 255);
  }
  #root{
    background-color: rgb(110, 110, 110)
  }